@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Signika+Negative:wght@300&family=Signika:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@600&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-family: 'Signika', sans-serif;
  font-family: 'Signika Negative', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FAFF81;
  /* background-image: url("https://st2.depositphotos.com/8392720/11641/v/950/depositphotos_116413272-stock-illustration-seamless-exotic-fruit-pattern.jpg"); */
  
}


code {

}

.tooltip_rect
{
  fill: #FFC53A;
}

.axis-label
{
  font-family: 'Signika Negative', sans-serif;
}


p {
  margin: 0 -20px -20px;
  padding: 15px;
  background-color: #B26700;
  font-weight: bold;
  text-align: center;
}


.tooltip {
  border-radius: 50%;
  border: 2px solid #FFC53A;
}


a {
  margin: 15px 0;
  padding-left: 30px;
  line-height: 1.5rem;
  list-style-type: square;
}

.App {
  text-align: center;

 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

